$activeColor:#1890ff;

.header {
  padding: 0.16rem 0.16rem 0;
  font-size: 18Px;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;

  .header-button {
    padding: 0.04rem 0.08rem;

    .header-nav {
      color: rgba(0, 0, 0, .65);
      text-decoration: none;

      &:hover {
        color: $activeColor;
      }
    }

    .active {
      color: $activeColor;
    }
  }
}